// main SASS file
@import 'variables';
@import 'mixins';

* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  &:after,
  &:before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
  }
}

html {
  @include font-sans(16px, null,null, 1.6);
}

h1,
h2,
h3,
h4 {
  @include font-serif();
}

ul {
  list-style: none;
}

/***********************************************
                SPECIAL
***********************************************/

.clearfix:after {
  display: table;
  clear: both;
  content: "";
}

/***********************************************
                LAYOUT
***********************************************/

.bg-header {
  background-color: #fff;
  background-color: #C7F7FF;
  color: #222;
  border-bottom: 1rem solid #89C0C9;
  border-top: 1rem solid #3a5E63;
  position: relative;

  &:before,
  &:after {
    content: "";
    position: absolute;
    bottom: -1rem;
    width: 30%;
    height: 1rem;
    background-color: #3a5E63;
  }

  &:before {
    left: 0;
  }

  &:after {
    right: 0;
  }
}



.bg-focal {
  background-color: #C9A689;
  color: #7C593C;
}

.bg-content1 {
  background-color: #fff;
  color: #222;
}

.bg-content2 {
  background-color: #C7F7FF;
  color: #3a5E63;
}

.bg-footer {
  background-color: #fff;
  color: #d4d4d4;
}

header {
  width: 400px;
  /* default */
  margin: 0 auto;
  position: relative;
}

header h1 {
  width: 100%;
  padding: 0 2rem;
}

header nav {
  width: 100%;
}

header nav > ul li {
  width: 100%;
  float: none;
}

section.focal {
  width: 400px;
  /* default */
  margin: 0 auto;
}

section.content1 {
  padding: 2rem 0;
}

section.content2 {
  padding: 2rem 0;
}

.row {
  clear: both;
}

.col-2 {
  width: 50%;
  float: left;
  padding-bottom: 2rem;
  border-left: 1px solid #3a5E63;
  margin-bottom: 2rem;
}

footer .col-2:first-child {
  border-left: 0;
}

footer {
  padding: 2rem 0;
}

footer p:last-child {}

/***********************************************
                NAVIGATION
***********************************************/

header nav {
  @extend .row;
  padding: 2rem 0;
}

header nav > ul > li > a {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #222;
  font-size: 1.414rem;
  letter-spacing: 0.25rem;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  padding: 1.0rem 2rem;
  line-height: 1;
  background-color: #fff;
}

header nav > ul > li > a:hover {
  font-weight: 700;
  color: #7C593C;
}

/***********************************************
                CONTENT
***********************************************/

header h1 {
  color: #7C593C;
  letter-spacing: 0.1rem;
}

header h1 i.fa-lg {
  display: block;
  width: 25%;
  float: left;
  font-size: 7rem;
  line-height: 12.7936rem;
  color: #C9A689;
}

header h1 i.fa-lg ~ span {
  float: right;
  width: 75%;
  font-size: 3.998rem;
  /**
    2 lines of 3.998 * 1.6 = 12.7936
    **/
  padding-left: 1rem;
  text-align: right;
}

.focal h2 {
  font-size: 1.999rem;
  line-height: 1.2;
  padding: 2rem 2rem 1rem 2rem;
}

.focal .author {
  font-size: 1rem;
  padding: 0.2rem 2rem 2rem;
}

.content1 h3 {
  font-size: 1.999rem;
  padding: 0.2rem 2rem 2rem;
}

.content1 h3 i {
  vertical-align: middle;
  padding-right: 2rem;
}

.content1 p {
  font-size: 1.414rem;
  padding: 0.5rem 2rem 2rem;
}

.content2 h3 {
  font-size: 1.999rem;
  padding: 1rem 2rem 0.5rem;
}

.content2 h4 {
  font-size: 1.999rem;
  padding: 1rem 2rem 0rem;
}

.content2 p {
  font-size: 1.414rem;
  padding: 0.5rem 2rem 1rem;
}

.content2 li {
  padding: 0.5rem 2rem;
  font-size: 1.414rem;
}

/***********************************************
                FOOTER
***********************************************/

footer h4 {
  font-size: 1.414rem;
  padding: 0.5rem 2rem;
  color: #7C593C;
}

footer p {
  font-size: 1rem;
  padding: 0.5rem 2rem;
  color: #222;
}

footer .fa-circle {
  color: #222;
}

footer h4[lang=de] {
  display: block;
}

footer h4[lang=en],
footer h4[lang=ar],
footer h4[lang=ru] {
  display: none;
}

@import 'queries';
